<script>
import {drumType, onDrumHit, highlights} from '@/js/drum'

export default {
  setup () {
    const hitClass = i => ({ hit: highlights[i]})
    return {hitClass, drumType, onDrumHit, highlights}
  }
}
</script>

<style lang="scss">
.drum-visual {
  position: relative;
  
  svg {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
    
    * {pointer-events: none;}

    [id*="highlight"] {
      pointer-events: fill;
      cursor: pointer;
      transition: fill 0.25s, filter 0.25s;
      
      &:hover {filter:brightness(0.7);}
    }

    .hit {
      transition: fill 0s;
      fill: rgb(99, 99, 99);
    }
  }

  .analog {
    [id*="crash"] [id*="highlight"].hit,
    [id*="drum"] [id*="highlight"].hit,
    #kick #highlight.hit { fill: #80dbff; }

    [id*="crash"] [id*="highlight"] { fill: rgb(110, 110, 110); }

    [id*="drum"] path { fill: #aaaaaa; }
    [id*="drum"] [id*="highlight"] { fill: #FFFFFF; }

    #kick path { fill: rgb(124, 124, 124); }
    #kick #highlight { fill: #FFFFFF }
  }
  
  .latin {
    [id*="crash"] [id*="highlight"].hit,
    [id*="drum"] [id*="highlight"].hit,
    #kick #highlight.hit { fill: #fe4444; }

    [id*="crash"] [id*="highlight"] { fill: rgb(119, 186, 56); }

    [id*="drum"] path { fill: #8b580c; }
    [id*="drum"] [id*="highlight"] { fill: #fff7cb; }

    #kick path { fill: rgb(122, 59, 0); }
    #kick #highlight { fill: #fff7cb; }
  }
}

</style>